.app {
  background-color: #209cee;
  padding: 30px;
  height: 100vh;
}

.todo-list{
 background: #e8e8e8;
 border-radius: 4px;
 padding:5px;
 max-width: 400px;
}

.todo{
background: #fff;
box-shadow: 1px 1px 1px rgba(0,0,0,0.15);
padding: 3px 10px;
margin-bottom: 6px;
border-radius: 3px;
display: flex;
align-items: center;
justify-content: space-around;
}

.input{
  width: 100%;
  box-sizing: border-box;
  border-radius:4px;
  box-shadow: none;
  padding: 1rem;
  height: 2.25em; 
}
